import playerShips from "../helpers/ships";
import { playerSquares } from "../DOMinteraction";

let shipNumber = 0;
let allShipsPlaced = false;

export const placeYourShips = function (board) {
  while (!allShipsPlaced) {
    playerSquares.forEach((sq) =>
      sq.addEventListener("click", () => addShip(board))
    );
  }
};

function addShip(board) {
  board.placeShip(playerShips[shipNumber]);
  shipNumber += 1;
  if (shipNumber === 4) {
    allShipsPlaced = true;
  }
}
